<template>
    <div>
        <router-view></router-view>
        <div class="propaganda-wrapper">
            <div class="propaganda">

            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
    import "../font/common/style.css"
    import "../font/user/style.css"
    import "../font/icon/iconfont.css"
    import "../lib/swiper.min.css"
    import "../lib/swiper.min.js"


    //v2版本的icon文件引入   ----刘洋
    import "./v3/css/style.css"

    export default {
        created(){
            var ua = window.navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                this.isWeixin=1;
                this.words='请长按识别二维码，关注公众号，'
            } else {

            }
        },
        data(){
          return {
              isWeixin:0,
              words:'请保存此二维码，发送到微信中，并长按识别，方便再次访问哟！'
          }
        },
        methods: {

        }
    }
</script>
<style scoped lang="scss">
    @import "../css/common.scss";
    .propaganda-wrapper{
        padding:0 pxToRem(110);
        position:fixed;
        z-index:10000;
        top:0;
        left:0;
        right:0;
        bottom:0;
        background:rgba(0,0,0,.3);
        display:flex;
        .propaganda{
            align-items:center;
            justify-content: center;
        }
    }
</style>



